import React, {CSSProperties} from "react";
import style from "./MyProjectsBlock.module.scss"
import styleContainer from "./../common/styles/container.module.css"
import Project from "./Project/Project";
import Title from "../common/components/title";
import todoImg from "../assets/image/photo-1507925921958-8a62f3d1a50d.jpg";
import socialImg from "../assets/image/7-best-social-networks-to-do-marketing-in-your-ecommerce.png"

type ProjectType = {
    title: string
    description: string
    link: string
    style: CSSProperties
}


function MyProjectsBlock() {
    const socialNetwork = {
        backgroundImage: `url(${socialImg})`
    }
    const todoList = {
        backgroundImage: `url(${todoImg})`
    };

    const projects: Array<ProjectType> = [
        {title: "TodoList", description: "some text", link: "", style: todoList},
        {title: "Social Network", description: "some text", link: "", style: socialNetwork}
    ]

    return (
        <div className={style.projectBlock}>
            <div className={`${styleContainer.container} ${style.projectsContainer}`}>
                <Title title={"My projects"}/>
                <div className={style.projects}>
                    {projects.map((p) =>
                        <Project
                            style={p.style}
                            key={p.title}
                            title={p.title}
                            description={p.description}
                            link={p.link}
                        />
                    )}
                </div>
            </div>
        </div>
    )
}

export default MyProjectsBlock